import React, { useEffect, useRef, useState } from 'react';
import CanvasBG from './CanvasBG';

export interface Color {
  offset: number;
  color: string;
}

interface Props {
  label: string;
  value: Color[];
  onChange: (event: React.ChangeEvent) => void;
  onColorbarClick: (offset: number) => void;
  onColorbarMoved: (value: Color[]) => void;
}

function getGradientStyle(colors: Color[]) {
  return colors.map(({ offset, color }) => {
    const percentage = offset * 100;
    return `${color} ${percentage}%`;
  });
}

function generateDragImageElement() {
  const dragImageElement = document.createElement('div');
  dragImageElement.style.opacity = '0';
  return dragImageElement;
}

const GradientInputBox = ({
  label,
  value,
  onChange,
  onColorbarClick,
  onColorbarMoved,
}: Props) => {
  const [width, setWidth] = useState<number>();
  const colorLineRef = useRef<HTMLDivElement>();

  let valueWithId = [...value].map((item, i) => ({ ...item, id: i }));

  let dragImage: HTMLElement = generateDragImageElement();
  let pointer: HTMLElement | undefined = undefined;

  useEffect(() => {
    const colorLine = colorLineRef.current;
    if (!colorLine) return;

    const { width } = colorLine.getBoundingClientRect();
    setWidth(width - 16);
  }, []);

  function onColorDragStart(event: React.DragEvent) {
    pointer = event.currentTarget as HTMLElement;

    //     
    document.body.appendChild(dragImage);
    event.dataTransfer.setDragImage(dragImage, 0, 0);
  }

  function onColorbarDragOver(event: React.DragEvent) {
    if (!pointer) return;

    const position = event.nativeEvent.offsetX;
    const offset = position / width!;
    const colorbar = event.nativeEvent.target as HTMLElement;

    valueWithId = valueWithId
      .map((color) => {
        return pointer!.dataset.id === String(color.id) ? { ...color, offset } : color;
      })
      .sort((a, b) => a.offset - b.offset);

    pointer.style.transform = `translateX(${position}px)`;
    colorbar.style.background = `linear-gradient(90deg, ${getGradientStyle(valueWithId)})`;
  }

  function onColorDragEnd() {
    pointer = undefined;

    const updated = valueWithId.map(({ offset, color }) => ({ offset, color }));
    onColorbarMoved && onColorbarMoved(updated);

    //         
    document.body.removeChild(dragImage!);
  }

  return (
    <div className="inputbox">
      <span className="inputbox-label">{label}</span>
      <div className="inputbox-gradient">
        <div className="gradient">
          <div
            ref={colorLineRef as React.MutableRefObject<HTMLDivElement>}
            className="gradient-line"
          >
            <CanvasBG width={240} height={34} />
            <div
              className="gradient-colorbar"
              style={{
                background: `linear-gradient(90deg, ${getGradientStyle(valueWithId)})`,
              }}
              onClick={(event: React.MouseEvent) => {
                const { offsetX } = event.nativeEvent;
                const offset = offsetX / width!;
                onColorbarClick(offset);
              }}
              onDragOver={onColorbarDragOver}
            ></div>
          </div>
          <div className="gradient-colors">
            {width &&
              value.map(({ offset, color }, i) => {
                const position = offset * width;
                return (
                  <button
                    key={color + offset + i}
                    className="gradient-color"
                    style={{ transform: `translateX(${position}px)`, background: color }}
                    draggable
                    onDragStart={onColorDragStart}
                    onDragEnd={onColorDragEnd}
                    data-id={i}
                  >
                    <div className="gradient-wrapper">
                      <input
                        className="gradient-input"
                        type="color"
                        value={color}
                        onChange={onChange}
                        data-id={i}
                      />
                    </div>
                  </button>
                );
              })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default GradientInputBox;
