import React, { useCallback, useState } from 'react';
import { CurvedLineType } from '../../library/fabric/curvedLine';
import InputContainer from './InputContainer';
import InputBox from './InputBox';
import ColorInputBox from './ColorInputBox';
import { matchColorFormat, toFillColor, toGradient, toTwoDigitsHex } from '../../utils/color';
import GradientInputBox, { Color } from './GradientInputBox';
import { fabric } from 'fabric';

interface Props {
  item: CurvedLineType;
  renderAll: () => void;
}

const LineControl = ({ item, renderAll }: Props) => {
  const { canvas } = item;
  const initialFillColor = toFillColor(item.fill);
  const initialGradient = toGradient(item.fill);

  const [amplitude, setAmplitude] = useState(item.amplitude);
  const [waveLength, setWaveLength] = useState(item.waveLength);
  const [fillColor, setFillColor] = useState(initialFillColor);
  const [centerX, setCenterX] = useState(item.centerX);
  const [cropLeft, setCropLeft] = useState(item.cropLeft);
  const [cropRight, setCropRight] = useState(item.cropRight);
  const [filledY, setFilledY] = useState(item.filledY || item.canvas.height);
  const [fillOpacity, setFillOpacity] = useState(100);
  const [gradient, setGradient] = useState<Color[]>(initialGradient);

  const addColorButton = useCallback(
    (offset) => {
      setGradient((prev) => {
        const updated = [...prev, { offset, color: '#ffffff' }];
        const sorted = updated.sort((a, b) => a.offset - b.offset);

        const gradientFill = new fabric.Gradient({
          type: 'linear',
          colorStops: sorted,
          gradientUnits: 'percentage',
          coords: { x1: 0, y1: 0, x2: 1, y2: 0 },
        });
        item.set('fill', gradientFill);
        setFillColor(toFillColor(updated[0].color));
        renderAll();

        return sorted;
      });
    },
    [item, renderAll]
  );

  return (
    <>
      <InputContainer
        Super={
          <InputBox
            label=""
            className="controller-input"
            name="amplitude"
            type="range"
            value={amplitude}
            min={-(canvas.height / 2)}
            max={canvas.height / 2}
            onChange={(event: React.ChangeEvent) => {
              const { value } = event.target as HTMLInputElement;
              item.set('amplitude', value);
              setAmplitude(value);
              renderAll();
            }}
          />
        }
        condition={Number(amplitude) !== 0}
        Sub={
          <>
            <InputBox
              label=""
              className="controller-input"
              name="waveLength"
              type="range"
              value={waveLength}
              min={2}
              max={canvas.width * 2}
              step={10}
              onChange={(event: React.ChangeEvent) => {
                const { value } = event.target as HTMLInputElement;
                item.set('waveLength', value);
                setWaveLength(value);
                renderAll();
              }}
            />
            <InputBox
              label=" "
              className="controller-input"
              name="centerX"
              type="range"
              value={centerX}
              min={0}
              max={Math.floor(waveLength * 2.5)}
              onChange={(event: React.ChangeEvent) => {
                const { value } = event.target as HTMLInputElement;
                item.set('centerX', value);
                setCenterX(value);
                renderAll();
              }}
            />
          </>
        }
      />
      <InputBox
        label=" "
        className="controller-input"
        name="cropLeft"
        type="range"
        value={cropLeft}
        min={0}
        max={canvas.width - cropRight}
        onChange={(event: React.ChangeEvent) => {
          const { value } = event.target as HTMLInputElement;
          item.set('cropLeft', value);
          setCropLeft(value);
          renderAll();
        }}
      />
      <InputBox
        label=" "
        className="reverse"
        name="cropRight"
        type="range"
        value={cropRight}
        min={0}
        max={canvas.width - cropLeft}
        onChange={(event: React.ChangeEvent) => {
          const { value } = event.target as HTMLInputElement;
          item.set('cropRight', value);
          setCropRight(value);
          renderAll();
        }}
      />
      <InputContainer
        Super={
          <ColorInputBox
            label=" "
            name="fillColor"
            value={fillColor}
            onChange={(event: React.ChangeEvent) => {
              const value = (event.target as HTMLInputElement).value.trim();
              setFillColor(value);
              setGradient(toGradient(value));

              const colorCode = matchColorFormat(value);

              if (colorCode) {
                const opacityHex = toTwoDigitsHex(fillOpacity);
                const colorCodeWithOpacity = colorCode + opacityHex;

                item.set('fill', colorCodeWithOpacity);
                item.set('filledY', filledY);
              } else if (!value) {
                item.set('fill', undefined);
              }
              renderAll();
            }}
            onResetClick={() => {
              setFillColor('');
              setGradient(toGradient(''));
              item.set('fill', undefined);
              renderAll();
            }}
          />
        }
        condition={Boolean(fillColor)}
        Sub={
          <>
            <InputBox
              label=" "
              className="controller-input"
              name="filledY"
              type="range"
              value={filledY}
              min={-canvas.height}
              max={canvas.height}
              onChange={(event: React.ChangeEvent) => {
                const { value } = event.target as HTMLInputElement;
                item.set('filledY', value);
                setFilledY(value);
                renderAll();
              }}
            />
            <InputBox
              label=" "
              className="control-panel-input"
              name="opacity"
              type="range"
              value={fillOpacity}
              min={0}
              max={100}
              onChange={(event: React.ChangeEvent) => {
                const value = Number((event.target as HTMLInputElement).value);
                setFillOpacity(value);

                const hex = toTwoDigitsHex(value);
                const colorCode = matchColorFormat(fillColor);

                if (colorCode) {
                  const colorCodeWithOpacity = colorCode + hex;
                  item.set('fill', colorCodeWithOpacity);
                  renderAll();
                }
              }}
            />
          </>
        }
      />
      <GradientInputBox
        label=""
        value={gradient}
        onChange={(event: React.ChangeEvent) => {
          const target = event.target as HTMLInputElement;
          const { value } = target;
          const colorButton = target.closest('.gradient-color') as HTMLButtonElement;
          const id = colorButton.dataset.id;

          setGradient((prev) => {
            const updated = [...prev].map((item, i) =>
              i === Number(id) ? { ...item, color: value } : item
            );

            const gradientFill = new fabric.Gradient({
              type: 'linear',
              colorStops: updated,
              gradientUnits: 'percentage',
              coords: { x1: 0, y1: 0, x2: 1, y2: 0 },
            });
            item.set('fill', gradientFill);
            setFillColor(toFillColor(updated[0].color));
            renderAll();

            return updated;
          });
        }}
        onColorbarClick={addColorButton}
        onColorbarMoved={setGradient}
      />
    </>
  );
};

export default LineControl;
