import React from 'react';
import remove from '../../assets/svg/close_black_24dp.svg';

interface Props extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  className?: string;
  name?: string;
  value?: any;
  onChange?: (event: React.ChangeEvent) => void;
  onResetClick?: (event: React.MouseEvent) => void;
}

const ColorInputBox = ({
  label,
  className,
  name,
  value,
  onChange,
  onResetClick,
  ...props
}: Props) => {
  const id = Math.floor(Date.now() * Math.random() * Math.pow(10, 3)).toString(16);

  const colorValue = value && value.length > 6 ? value.slice(0, 7) : value;
  return (
    <div className="inputbox color">
      <label className="inputbox-label" htmlFor={id}>
        {label}
      </label>
      <div className="inputbox-wrapper">
        <input
          id={id}
          className="inputbox-input color"
          type="text"
          value={value}
          placeholder=" "
          onChange={onChange}
        />
        {value && (
          <button className="reset" onClick={onResetClick}>
            <img className="icon" src={remove} alt="" />
          </button>
        )}
      </div>
      <div className="inputbox-sub color">
        <div className="inputbox-color" style={{ background: value }}>
          <input
            className="inputbox-color-input"
            name={name}
            type="color"
            value={colorValue}
            onChange={onChange}
            {...props}
          />
        </div>
      </div>
    </div>
  );
};

export default ColorInputBox;
