import { fabric } from 'fabric';

export function matchColorFormat(value?: string) {
  if (!value) return null;

  const matches = value.match(/\B#[0-9a-f]{6}|\B#[0-9a-f]{3}/i);

  if (matches === null) {
    return null;
  } else {
    const match = matches[0];
    let colorCode = match;

    if (colorCode.length < 5) {
      colorCode = Array.from(colorCode.slice(1)).reduce((result: string, char: string) => {
        return result + char.repeat(2);
      }, '#');
    }

    return colorCode;
  }
}

export function toTwoDigitsHex(value: number) {
  const hex = Math.floor((value / 100) * 255).toString(16);
  const twoDigitsHex = hex.length < 2 ? '0' + hex : hex;
  return twoDigitsHex;
}

export function toFillColor(fillColor: any) {
  return typeof fillColor === 'string' ? fillColor : undefined;
}

export function toGradient(fillColor: any) {
  return fillColor instanceof fabric.Gradient
    ? fillColor.toObject({}).colorStops
    : [
        { offset: 0, color: fillColor || 'transparent' },
        { offset: 1, color: fillColor || 'transparent' },
      ];
}
